.root {
  position: relative;
  margin: 0 20px;
  // left: -120px;
}

.input {
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  padding: 12px 20px;
  padding-left: 42px;
  width: 400px;
  font-size: 16px;
  transition: all 0.15s ease-in-out;

  //добавил
  @media (max-width: 600px) {
    width: 300px;
  }

  //   &:focus {
  //     border: 1px solid rgba(0, 0, 0, 0.2);
  //     width: 400px;
  //   }
}

.icon {
  width: 22px;
  height: 22px;
  opacity: 0.3;
  position: absolute;
  left: 14px;
  top: 12px;
}

.clearIcon {
  width: 18px;
  height: 18px;
  opacity: 0.3;
  position: absolute;
  right: 15px;
  top: 15px;
  cursor: pointer;

  &:hover {
    opacity: 0.8;
  }
}
